.mainSection {
  width: 90vw;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 60vh;
  margin: 0 auto;
  transform: translateY(50px);

  .text {
    width: 40%;
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    flex-direction: column;
    color: #fff;
    font-weight: 400;
    text-transform: capitalize;

    h1 {
      color: #fff;
      font-weight: 400;
      font-size: 55px;
    }
    a {
      color: #1affab;
      border: 1px solid #1affab;
      padding: 10px 25px;
      text-decoration: none;
      font-size: 20px;
      font-weight: 400;

      transition: 1s ease;

      &:hover {
        background: linear-gradient(90deg, #1affab, #1abeab);
        border-radius: 25px;
        color: #fff;
      }
    }
  }

  .img {
    width: 60%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;

    .mainImgContainer {
      width: 100%;
      height: 100%;
      background: url(./cont.svg) no-repeat center center / contain;
      display: flex;
      align-items: center;
      justify-content: center;

      .mainImg {
        width: 80%;
        height: 80%;
        background: url(./mainImg.png) no-repeat center center / cover;
      }
    }
  }
}

@media screen and (max-width: 1192px) {
  .mainSection {
    .text {
      h1 {
        font-size: 35px;
      }
    }

    .img {
      .mainImgContainer {
        width: 80%;
        height: 80%;
        .mainImg {
          width: 95%;
          height: 95%;
        }
      }
    }
  }
}

@media screen and (max-width: 992px) {
}
@media screen and (max-width: 768px) {
  .mainSection {
    display: flex;
    flex-direction: column-reverse;
    height: 80vh;
    transform: none;

    .text {
      width: 100%;
      height: 40%;
      align-items: center;

      transform: translateY(15px);
      h1 {
        font-size: 25px;
      }
    }
    .img {
      width: 100%;
      height: 50%;

      .mainImgContainer {
        width: 80%;
        height: 100%;
        .mainImg {
          width: 85%;
          height: 100%;
          background-size: cover;
        }
      }
    }
  }
}

@media screen and (max-width: 600px) {
  .mainSection {
    display: flex;
    flex-direction: column-reverse;
    height: 100%;

    .text {
      width: 100%;
      height: 45vh;
      align-items: center;
      justify-content: center;

      transform: translateY(15px);
      h1 {
        font-size: 25px;
      }
    }
    .img {
      width: 100%;
      height: 50vh;

      .mainImgContainer {
        width: 60%;
        height: 100%;
        .mainImg {
          width: 100%;
          height: 100%;
          background-size: contain;
        }
      }
    }
  }
}
@media screen and (max-width: 500px) {
  .mainSection {
    width: 100vw;
    transform: translateY(10px);

    .mainImg {
      .mainImgContainer {
        width: 60%;
        height: 80%;
      }
    }
  }
}

